'use strict';

const e = React.createElement;
class TradingLog extends React.Component {
  constructor(props) {
    super(props);
    this.state = {
      data: [
        {
          id: 0,
          value: "01122020",
          date: "December 2020"
        },
        {
          id: 1,
          value: "01012021",
          date: "Januari 2021"
        },
      ],
    };
  }

  render() {
    return (
      <div className="trading-log">
        <div className="trading-log__heading">
          <select className="select select--naked color-lightgrey">
            {this.state.data.map((item) => (
              <option key={item.id} value={item.value}>{item.date}</option>
            ))}
          </select>
        </div>
        <div className="trading-log__body">
          <span className="color-lightgrey">{this.props.name}</span>
        </div>
      </div>
    );
  }
}


document.querySelectorAll('#tradinglogComponent').forEach(domContainer => { 
  const nameProp = domContainer.dataset.name;
  ReactDOM.render(
    e(TradingLog, {name: nameProp}), 
  domContainer);	
});